body {
  --color-text: #00acdc;
  text-transform: uppercase;
  margin: 0;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-style: normal;
  font-weight: 500;
  line-height: normal;
}

.header {
  justify-content: space-between;
  align-items: center;
  max-width: 100%;
  padding: 0 50px;
  display: flex;
}

.link-image {
  width: 40px;
  height: 40px;
}

.nav-list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav-item {
  margin-right: 20px;
}

.nav-item:last-child {
  margin-right: 0;
}

.nav-link {
  color: #000;
  align-items: center;
  height: 60px;
  text-decoration: none;
  display: flex;
}

.is-active {
  color: var(--color-text);
  position: relative;
}

.is-active:after {
  content: "";
  background-color: var(--color-text);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  display: block;
  position: absolute;
  bottom: 0;
}

.nav-link:hover {
  color: var(--color-text);
}

